
<template>
<div id="appticket_type">
<form  @submit.prevent="submit">
    <div class="row justify-content-center">
        <div class="col-10 p-3 shadow rounded">
            <div v-if="forms">
            <p class="p-3 text-xl font-semibold">Form Table ticket_type : </p>
            <hr class="my-2">

                <div class="sm-form">
                    <label for="id">id</label>
                    <input  disabled type="Disabled" id="id" name="id" class="form-control form-control-sm" placeholder="id" v-model="vdata['id']" >
                </div>
    
                <div class="sm-form">
                    <label for="ticket_type">ticket_type</label>
                    <input required  type="Text" id="ticket_type" name="ticket_type" class="form-control form-control-sm" placeholder="ticket_type" v-model="vdata['ticket_type']" >
                </div>
    
                <hr class="style2">
                <!-- BUTTON FORM -->
                <div class="my-3">
                    <button type="submit" class="btn btn-sm capitalize " :class="btn == 'tambah'? 'btn-primary': btn == 'update'? 'btn-success': 'btn-danger'">
                        <span class="typcn typcn-info"></span> @{{ btn }}
                    </button>
                    <div class="float-right">
                        <button type="button" v-if="btn!='tambah'" @click="btn = 'tambah';vdata = {};"
                        class="btn btn-sm btn-dark">
                            <span class="typcn typcn-edit"></span> Refresh
                        </button>
                        <button type="button" v-if="btn!='tambah'" @click="btn = 'delete'" class="ml-3 btn btn-sm btn-danger">
                            <span class="typcn typcn-delete"></span> Delete
                        </button>
                    </div>
                 </div>
               
            </div>
            <div v-if="btable" class="shadow rounded p-3">
                <div class="p-3">
                    <button type="button" @click="forms=!forms" class="btn btn-sm btn-success  ">@{{forms?'Hide':'Show'}} Form</button>
                </div>
                <Btables :tables="datanya" :hide="['upload','created_at','id','updated_at','deleted_at','file','gambar']" 
                @selected="ambil" @hapusdata="hapusData" @hapussemuadata="hapusSemuaData" @pdf="pdf" @excel="excel"/>
            </div>
     </div>
    </div>
</form>
</div>
</template>
<script type="module">
    import Btables from '@/components/Btables'
    import autophp from '@/plugins/autophp' 
    let sdb = new autophp();
    import firebase from 'firebase'
    let db = firebase.firestore();
    export default {
    layout:"hit-mobile",
    data(){
        return{
            vdata:{},
            btn: "tambah",
            content:"",
            datanya:[],
            relationship:{},
            btable:false,
            forms:false
        }
    },
    components:{
        Btables:Btables
    },
    methods: {
        async getAlldata(data){
            let datas = await db.collection(`${data}`).get()
            datas = datas.docs.map(doc=>{return {id:doc.id,...doc.data()}})
            this.relationship[data]=datas;
            return hasil;
        },
        pdf(){
            
        },
        excel(){
            var filename='reports.xls';
            var data=this.datanya
            var ws = XLSX.utils.json_to_sheet(data);
            var wb = XLSX.utils.book_new();
            XLSX.utils.book_append_sheet(wb, ws, "People");
            XLSX.writeFile(wb,filename);
        },
        hapusData(){
            let that=this;
            if(confirm('Apakah hapus data ?')){
                db.collection("ticket_type").doc(this.vdata.id).delete().then(res=>{
                    that.getData();
                    sdb.alert('Delete Data Berhasil','bg-green-400');
                }).catch(err=>{
                    sdb.alert('Delete Data Gagal','bg-red-400');
                })
            }
        },
        hapusSemuaData(data){
            let that=this;
            if(confirm('Apakah hapus Semua data ?')){
                data.forEach(e=>{
                    db.collection("ticket_type").doc(e.id).delete().then(res=>{
                        sdb.alert('Delete Data Berhasil','bg-green-400');
                    }).catch(err=>{
                        sdb.alert('Delete Data Gagal','bg-red-400');
                    })
                })
                setTimeout(() => {
                    that.getData();
                }, 2000);
            }
        },
        upload(id){
            let file = document.querySelector(`#${id}`); // berikan id pada input file
            file = document.querySelector("#filenya"); // berikan id pada input file
            sdb.collection('tbuser',false).upload(file,false,true) // file,compress,scramble
            .then(res=>{
                this.vdata[`${id}`]=res;
                this.$forceUpdate();
                sdb.alert('Upload Data Berhasil','bg-green-400');
            })
        },     
        ambil(data){
            this.vdata=JSON.parse(JSON.stringify(data))
            this.btn="update"
            this.forms=true;
            this.$forceUpdate()
        },
        getData(){
            let that=this
            that.btable=false
            db.collection("ticket_type").get().then(res=>{
                let data = res.docs.map(doc=>{return {
                    id:doc.id,
                    ...doc.data()
                }})
                that.datanya=data;
                that.btable=true
                that.$forceUpdate()
                sdb.alert('Get Data Berhasil','bg-green-400');
            }).catch(err=>{
                that.btable=true
                sdb.alert('Get Data Gagal','bg-red-400');
            })
        },
        submit(){
            let that=this
                if (confirm("Apakah yakin proses dilanjutkan ?")) {
                if (this.btn == "tambah") {
                    delete this.vdata.id;
                    db.collection("ticket_type").doc().set(this.vdata,{merge:true}).then(res=>{
                        that.getData();
                        sdb.alert('Add Data Berhasil','bg-green-400');
                    }).catch(err=>{
                        sdb.alert('Add Data Gagal','bg-red-400');
                    })
                } else if (this.btn == "update") {
                    db.collection("ticket_type").doc(this.vdata.id).set(this.vdata,{merge:true}).then(res=>{
                        that.getData();
                        sdb.alert('Update Data Berhasil','bg-green-400');
                    }).catch(err=>{
                        sdb.alert('Update Data Gagal','bg-red-400');
                    })
                } else if (this.btn == "delete") {
                    db.collection("ticket_type").doc(this.vdata.id).delete().then(res=>{
                        that.getData();
                        sdb.alert('Delete Data Berhasil','bg-green-400');
                    }).catch(err=>{
                        sdb.alert('Delete Data Gagal','bg-red-400');
                    })
                }
            }
        }
    },
    mounted() {
        this.getData();
        
        },
    }
  </script>
